.button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-family: var(--font-family-primary);
  color: var(--lm-gray-mid-1);
  fill: currentColor;
  background-color: var(--lm-gray-light-1);
  font-weight: 700;
  font-size: 11px;
  line-height: 12px;
  height: 28px;
  padding: 0 12px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: opacity 0.2s ease-in-out;
  outline: none;

  &:hover {
    opacity: 0.7;
  }

  &:active {
    opacity: 0.5;
  }

  &:focus-visible {
    box-shadow: inset 0 0 0 2px var(--lm-blue);
  }

  &__inner {
    display: inherit;
    align-items: inherit;
    justify-content: inherit;
  }

  &__start-icon {
    display: inherit;
  }

  &--primary {
    color: var(--lm-white);
    background-color: var(--lm-black);
  }

  &--with-icon {
    padding: 8px;
    .button__start-icon {
      margin: 0;
    }

    .button__text {
      display: none;
    }

    @media (min-width: 992px) {
      padding: 0 12px;
      .button__start-icon {
        margin-right: 8px;
      }
      .button__text {
        display: initial;
      }
    }
  }

  &--only-icon {
    padding: 8px;
    .button__start-icon {
      margin: 0;
    }
  }

  svg {
    fill: inherit;
  }
}
